.story-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 0;
  overflow: hidden;
}

.story-chapter {
  position: absolute;
  inset: 0;
  will-change: opacity;
}

// Gradient layers for each chapter
.gradient-layer {
  position: absolute;
  inset: 0;
}

.dawn-gradient {
  background: linear-gradient(
    180deg,
    #fdf8f3 0%,
    #faf3eb 30%,
    #f5ebe0 60%,
    #efe4d8 100%
  );
}

.growth-gradient {
  background: linear-gradient(
    180deg,
    #f0ebe4 0%,
    #e8e3d8 30%,
    #dfd8cc 60%,
    #d6cfc2 100%
  );
}

.journey-gradient {
  background: linear-gradient(
    180deg,
    #e8e0d5 0%,
    #ddd4c7 30%,
    #d4c9ba 60%,
    #ccc0b0 100%
  );
}

.horizon-gradient {
  background: linear-gradient(
    180deg,
    #ddd5c8 0%,
    #d0c7b8 30%,
    #c5baa8 60%,
    #bfb39f 100%
  );
}

// Floating elements container
.floating-elements {
  position: absolute;
  inset: 0;
}

// Chapter 1: Dawn elements
.story-element.sun {
  position: absolute;
  top: 8%;
  right: 12%;
  width: 80px;
  height: 80px;
  background: radial-gradient(circle, rgba(212, 163, 115, 0.4) 0%, rgba(212, 163, 115, 0.1) 50%, transparent 70%);
  border-radius: 50%;

  &::before {
    content: '';
    position: absolute;
    inset: 15px;
    background: radial-gradient(circle, rgba(196, 149, 106, 0.5) 0%, rgba(196, 149, 106, 0.2) 60%, transparent 100%);
    border-radius: 50%;
  }
}

.story-element.cloud {
  position: absolute;
  background: rgba(255, 255, 255, 0.3);
  border-radius: 50px;
  filter: blur(8px);

  &.cloud-1 {
    top: 15%;
    left: 10%;
    width: 120px;
    height: 40px;
  }

  &.cloud-2 {
    top: 22%;
    right: 25%;
    width: 80px;
    height: 30px;
  }
}

// Chapter 2: Growth elements
.story-element.tree {
  position: absolute;
  bottom: 30%;
  left: 8%;
  width: 3px;
  height: 100px;
  background: linear-gradient(180deg, rgba(168, 144, 120, 0.3) 0%, rgba(168, 144, 120, 0.1) 100%);
  transform-origin: bottom center;

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 60px;
    height: 60px;
    background: radial-gradient(circle, rgba(168, 144, 120, 0.2) 0%, transparent 70%);
    border-radius: 50%;
  }
}

.story-element.leaf {
  position: absolute;
  width: 12px;
  height: 12px;
  background: rgba(168, 144, 120, 0.3);
  border-radius: 0 50% 50% 50%;

  &.leaf-1 {
    top: 20%;
    left: 20%;
  }

  &.leaf-2 {
    top: 15%;
    right: 30%;
    background: rgba(181, 116, 90, 0.25);
  }
}

// Chapter 3: Journey elements
.story-element.mountain {
  position: absolute;
  bottom: 20%;
  right: 10%;
  width: 0;
  height: 0;
  border-left: 60px solid transparent;
  border-right: 60px solid transparent;
  border-bottom: 80px solid rgba(139, 115, 85, 0.1);

  &::before {
    content: '';
    position: absolute;
    top: 30px;
    left: -40px;
    width: 0;
    height: 0;
    border-left: 40px solid transparent;
    border-right: 40px solid transparent;
    border-bottom: 55px solid rgba(139, 115, 85, 0.08);
  }
}

.story-element.path {
  position: absolute;
  bottom: 25%;
  left: 0;
  height: 2px;
  background: linear-gradient(90deg,
    transparent 0%,
    rgba(139, 115, 85, 0.2) 20%,
    rgba(196, 149, 106, 0.15) 80%,
    transparent 100%
  );
  max-width: 100%;
}

// Chapter 4: Horizon elements
.story-element.stars {
  position: absolute;
  inset: 0;
  background-image:
    radial-gradient(circle at 20% 30%, rgba(196, 149, 106, 0.3) 1px, transparent 1px),
    radial-gradient(circle at 80% 20%, rgba(196, 149, 106, 0.25) 1px, transparent 1px),
    radial-gradient(circle at 40% 60%, rgba(181, 116, 90, 0.2) 1px, transparent 1px),
    radial-gradient(circle at 70% 70%, rgba(196, 149, 106, 0.3) 1px, transparent 1px),
    radial-gradient(circle at 10% 80%, rgba(139, 115, 85, 0.25) 1px, transparent 1px),
    radial-gradient(circle at 90% 50%, rgba(196, 149, 106, 0.2) 1px, transparent 1px);
}

.story-element.moon {
  position: absolute;
  top: 15%;
  right: 15%;
  width: 50px;
  height: 50px;
  background: radial-gradient(circle at 30% 30%,
    rgba(255, 252, 247, 0.6) 0%,
    rgba(235, 230, 223, 0.4) 50%,
    transparent 70%
  );
  border-radius: 50%;
  box-shadow: 0 0 40px rgba(196, 149, 106, 0.2);
}

// Brush strokes
.brush-strokes {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.brush-stroke {
  position: absolute;
  height: 3px;
  background: linear-gradient(90deg,
    transparent 0%,
    rgba(196, 149, 106, 0.15) 30%,
    rgba(181, 116, 90, 0.1) 70%,
    transparent 100%
  );
  border-radius: 2px;
  overflow: hidden;

  &.stroke-1 {
    top: 25%;
    left: 0;
    max-width: 60%;
    transform: rotate(-2deg);
  }

  &.stroke-2 {
    top: 55%;
    right: 0;
    left: auto;
    max-width: 50%;
    transform: rotate(1deg);
    background: linear-gradient(270deg,
      transparent 0%,
      rgba(139, 115, 85, 0.12) 30%,
      rgba(168, 144, 120, 0.08) 70%,
      transparent 100%
    );
  }

  &.stroke-3 {
    top: 80%;
    left: 0;
    max-width: 70%;
    transform: rotate(-1deg);
  }
}

// Chapter indicator
.chapter-indicator {
  position: fixed;
  right: 2rem;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column;
  gap: 1rem;
  z-index: 100;
  pointer-events: auto;
}

.indicator-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgba(139, 115, 85, 0.2);
  border: 1px solid rgba(139, 115, 85, 0.3);
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;

  &:hover,
  &.active {
    background: rgba(139, 115, 85, 0.5);
    transform: scale(1.2);

    .indicator-label {
      opacity: 1;
      transform: translateX(-10px);
    }
  }

  &.active {
    background: #8b7355;
    border-color: #8b7355;
  }
}

.indicator-label {
  position: absolute;
  right: 100%;
  top: 50%;
  transform: translateY(-50%) translateX(0);
  margin-right: 1rem;
  font-size: 0.7rem;
  color: #8b7355;
  text-transform: uppercase;
  letter-spacing: 1px;
  white-space: nowrap;
  opacity: 0;
  transition: all 0.3s ease;
  pointer-events: none;
}

// Hide on mobile
@media (max-width: 768px) {
  .chapter-indicator {
    display: none;
  }

  .story-element.sun {
    width: 50px;
    height: 50px;
  }

  .story-element.cloud {
    display: none;
  }
}
