.contact-section-light {
  min-height: 100vh;
  padding: 100px 2rem;
  position: relative;
  overflow: hidden;
  background: transparent;
}

.contact-container {
  max-width: 1000px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

// Chapter Header - Story-driven styling
.chapter-header {
  text-align: center;
  margin-bottom: 3rem;
}

.chapter-number {
  display: inline-block;
  font-size: 0.7rem;
  font-weight: 400;
  color: #8b7355;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 0.75rem;
  font-style: italic;
}

.chapter-title {
  font-size: clamp(2rem, 5vw, 3rem);
  font-weight: 300;
  color: #3d3d3d;
  margin-bottom: 0.75rem;
  letter-spacing: -0.02em;
}

.chapter-subtitle {
  font-size: 0.95rem;
  color: #7a7a7a;
  max-width: 400px;
  margin: 0 auto;
  line-height: 1.7;
}

// Contact Cards Grid
.contact-cards-grid {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: 3rem;
  margin-top: 2rem;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}

// Contact Methods
.contact-methods {
  .section-label {
    font-size: 0.75rem;
    font-weight: 500;
    color: #8b7355;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin-bottom: 1.5rem;
    display: block;
  }
}

.methods-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.method-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem 1.25rem;
  background: rgba(245, 242, 237, 0.6);
  border-radius: 4px;
  text-decoration: none;
  transition: all 0.3s ease;
  border: 1px solid transparent;

  &:hover {
    background: rgba(245, 242, 237, 0.9);
    border-color: rgba(139, 115, 85, 0.15);

    .method-arrow {
      opacity: 1;
      transform: translateX(0);
    }
  }
}

.method-icon {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(139, 115, 85, 0.08);
  border-radius: 50%;
  color: #8b7355;
  font-size: 0.9rem;
}

.method-info {
  flex: 1;
}

.method-label {
  display: block;
  font-size: 0.65rem;
  color: #9a9a9a;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 0.2rem;
}

.method-value {
  font-size: 0.875rem;
  font-weight: 500;
  color: #3d3d3d;
}

.method-arrow {
  color: #c4956a;
  font-size: 0.9rem;
  opacity: 0;
  transform: translateX(-5px);
  transition: all 0.3s ease;
}

// Right Column
.contact-right {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

// Resume Card - Artistic styling
.resume-card-artistic {
  position: relative;
  padding: 2rem;
  background: rgba(245, 242, 237, 0.6);
  border-radius: 4px;
  overflow: hidden;
}

.card-brush-stroke {
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: linear-gradient(
    180deg,
    transparent 0%,
    #c4956a 20%,
    #8b7355 50%,
    #b5745a 80%,
    transparent 100%
  );
}

.resume-card-artistic .card-content {
  .card-label {
    font-size: 0.65rem;
    color: #9a9a9a;
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  h4 {
    font-size: 1.25rem;
    font-weight: 500;
    color: #3d3d3d;
    margin: 0.5rem 0;
  }

  p {
    font-size: 0.85rem;
    color: #7a7a7a;
    line-height: 1.6;
    margin-bottom: 1.25rem;
  }
}

.resume-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: #8b7355;
  text-decoration: none;
  font-size: 0.875rem;
  font-weight: 500;
  transition: color 0.3s ease;

  svg {
    font-size: 0.9rem;
  }

  &:hover {
    color: #6b5344;
  }
}

// Closing Quote
.closing-quote {
  padding: 1.5rem;
  background: transparent;
  border-left: 2px solid rgba(139, 115, 85, 0.15);

  blockquote {
    font-size: 0.95rem;
    font-style: italic;
    color: #5a5a5a;
    line-height: 1.7;
    margin: 0 0 0.75rem;
  }

  cite {
    font-size: 0.75rem;
    color: #9a9a9a;
    font-style: normal;
  }
}

// Artistic Footer
.artistic-footer {
  text-align: center;
  margin-top: 4rem;
  padding-top: 2rem;
}

.footer-line {
  width: 60px;
  height: 1px;
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(139, 115, 85, 0.3) 50%,
    transparent 100%
  );
  margin: 0 auto 1.5rem;
}

.artistic-footer p {
  font-size: 0.8rem;
  color: #7a7a7a;
  margin-bottom: 0.25rem;
}

.footer-note {
  font-size: 0.7rem;
  color: #9a9a9a;
  font-style: italic;
}

// Mobile adjustments
@media (max-width: 768px) {
  .contact-section-light {
    padding: 80px 1.5rem;
  }

  .chapter-title {
    font-size: 2rem;
  }

  .method-item {
    padding: 0.875rem 1rem;
  }
}
