.hero-section-light {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  background: transparent;
}

.hero-content-light {
  text-align: center;
  z-index: 10;
  max-width: 800px;
  padding: 2rem;
}

.hero-brush-deco {
  width: 150px;
  height: 20px;
  margin: 0 auto 1.5rem;
}

.chapter-label {
  display: inline-block;
  font-size: 0.75rem;
  font-weight: 400;
  color: #8b7355;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 2rem;
  font-style: italic;
}

.name-container {
  position: relative;
  margin-bottom: 1.5rem;
}

.hero-name {
  font-size: clamp(4rem, 12vw, 9rem);
  font-weight: 300;
  line-height: 0.9;
  letter-spacing: -0.02em;
}

.name-line {
  display: block;
}

.name-first {
  color: #3d3d3d;
  font-weight: 300;
}

.name-last {
  color: #8b7355;
  font-weight: 500;
  font-style: italic;
}

.ink-splatter {
  position: absolute;
  top: 50%;
  right: -20px;
  width: 40px;
  height: 40px;
  background: radial-gradient(
    ellipse at center,
    rgba(196, 149, 106, 0.15) 0%,
    rgba(196, 149, 106, 0.08) 40%,
    transparent 70%
  );
  border-radius: 50% 60% 40% 50%;
  transform: translateY(-50%) rotate(15deg);
}

.role-container {
  margin-bottom: 2rem;
}

.role-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.role-text {
  font-size: 1rem;
  font-weight: 400;
  color: #5a5a5a;
  letter-spacing: 1px;
  overflow: hidden;
  white-space: nowrap;
}

.role-divider {
  color: #c4956a;
  font-size: 1.5rem;
}

.story-excerpt {
  font-size: 1rem;
  color: #7a7a7a;
  line-height: 1.8;
  max-width: 500px;
  margin: 0 auto 2.5rem;
}

.story-highlight {
  display: inline;
  font-weight: 500;
  color: #5a5a5a;
  background: linear-gradient(
    to right,
    rgba(196, 149, 106, 0.2) 0%,
    rgba(196, 149, 106, 0.2) 100%
  );
  background-repeat: no-repeat;
  background-position: left center;
  padding: 0.1em 0.2em;
  margin-left: -0.2em;
}

.cta-container {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

// Primary button with ink fill effect
.cta-brush-primary {
  position: relative;
  padding: 1rem 2.5rem;
  background: #8b7355;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.3s ease;

  .btn-text {
    position: relative;
    z-index: 1;
    font-size: 0.9rem;
    font-weight: 500;
    color: white;
    letter-spacing: 0.5px;
  }

  .btn-ink-fill {
    position: absolute;
    inset: 0;
    background: #6b5344;
    transform-origin: left;
    z-index: 0;
  }

  &:hover {
    box-shadow: 0 8px 25px rgba(139, 115, 85, 0.25);
  }
}

// Secondary button with brush underline
.cta-outline {
  position: relative;
  padding: 1rem 2rem;
  background: transparent;
  border: none;
  cursor: pointer;

  .btn-text {
    font-size: 0.9rem;
    font-weight: 500;
    color: #8b7355;
    letter-spacing: 0.5px;
  }

  .btn-underline {
    position: absolute;
    bottom: 8px;
    left: 15%;
    right: 15%;
    height: 2px;
    background: linear-gradient(
      90deg,
      transparent 0%,
      #c4956a 20%,
      #8b7355 50%,
      #c4956a 80%,
      transparent 100%
    );
    transform-origin: left;
    border-radius: 1px;
  }

  &:hover .btn-text {
    color: #6b5344;
  }
}

// Scroll prompt
.scroll-prompt {
  position: absolute;
  bottom: 3rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.75rem;
}

.scroll-text {
  font-size: 0.7rem;
  font-weight: 400;
  color: #9a9a9a;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.scroll-line {
  width: 1px;
  height: 40px;
  background: linear-gradient(
    180deg,
    transparent 0%,
    #c4956a 30%,
    #8b7355 70%,
    transparent 100%
  );
  transform-origin: top;
}

// Responsive
@media (max-width: 768px) {
  .hero-name {
    font-size: clamp(3rem, 15vw, 5rem);
  }

  .role-wrapper {
    flex-direction: column;
    gap: 0.5rem;

    .role-divider {
      display: none;
    }
  }

  .cta-container {
    flex-direction: column;
    align-items: center;
  }

  .cta-brush-primary,
  .cta-outline {
    width: 100%;
    max-width: 250px;
  }
}
