// Light Theme - Dimmed / Warm / Muted Aesthetic
// Soft earth tones, cream backgrounds, gentle contrast

:root[data-theme="light"] {
  // Primary colors - muted, warm
  --primary: #8b7355;           // Warm brown
  --primary-light: #a89078;     // Light taupe
  --primary-dark: #6b5344;      // Dark brown

  // Accent colors - earthy, soft
  --accent-warm: #c4956a;       // Terracotta
  --accent-rust: #b5745a;       // Rust
  --accent-tan: #a89078;        // Warm tan
  --accent-clay: #9a8575;       // Clay

  // Background colors - cream, warm grays
  --bg-primary: #f5f2ed;        // Warm cream
  --bg-secondary: #ebe6df;      // Darker cream
  --bg-tertiary: #e0dbd4;       // Warm gray
  --bg-card: rgba(255, 252, 247, 0.85);
  --bg-glass: rgba(245, 242, 237, 0.9);

  // Text colors - soft, not pure black
  --text-primary: #3d3d3d;      // Charcoal
  --text-secondary: #5a5a5a;    // Medium gray
  --text-muted: #7a7a7a;        // Light gray
  --text-light: #9a9a9a;

  // Borders
  --border-color: rgba(139, 115, 85, 0.2);
  --border-light: rgba(139, 115, 85, 0.1);

  // Shadows - warm tinted
  --shadow-sm: 0 2px 10px rgba(100, 80, 60, 0.06);
  --shadow-md: 0 8px 30px rgba(100, 80, 60, 0.08);
  --shadow-lg: 0 20px 50px rgba(100, 80, 60, 0.1);

  // Gradients - subtle, warm
  --gradient-primary: linear-gradient(135deg, #8b7355 0%, #c4956a 100%);
  --gradient-warm: linear-gradient(135deg, #c4956a 0%, #b5745a 100%);
}

[data-theme="light"] {
  background: var(--bg-primary);
  color: var(--text-primary);

  .matrix-background {
    display: none;
  }

  // Navbar - complete overrides for light theme
  .navbar {
    background: rgba(245, 242, 237, 0.95);
    backdrop-filter: blur(20px);
    border-bottom: 1px solid var(--border-light);

    &.scrolled {
      background: rgba(245, 242, 237, 0.98);
      box-shadow: 0 4px 30px rgba(139, 115, 85, 0.1);
    }

    .nav-logo {
      color: var(--text-primary);

      &:hover {
        text-shadow: 0 0 10px rgba(139, 115, 85, 0.3);
      }

      .cursor {
        color: var(--primary);
      }
    }

    .nav-link {
      color: var(--text-secondary);
      position: relative;

      .link-underline {
        display: none; // Hide the default underline
      }

      &:hover {
        color: var(--primary);
      }

      // Brush stroke active effect for light theme
      &.active {
        color: var(--primary);
        background: transparent;

        // Remove all the dark theme effects
        &::before,
        &::after {
          display: none;
        }

        // Ink brush stroke behind text
        .link-text {
          position: relative;

          &::before {
            content: '';
            position: absolute;
            left: -8px;
            right: -8px;
            top: 50%;
            height: 60%;
            transform: translateY(-50%) rotate(-1deg);
            background: linear-gradient(
              90deg,
              transparent 0%,
              rgba(196, 149, 106, 0.25) 15%,
              rgba(196, 149, 106, 0.35) 50%,
              rgba(196, 149, 106, 0.25) 85%,
              transparent 100%
            );
            border-radius: 2px 8px 4px 6px;
            z-index: -1;
          }

          // Ink splatter dot
          &::after {
            content: '';
            position: absolute;
            right: -12px;
            top: 0;
            width: 4px;
            height: 4px;
            background: var(--accent-rust);
            border-radius: 50%;
            opacity: 0.6;
          }
        }
      }
    }
  }

  .mobile-menu-btn {
    color: var(--primary);

    &:hover {
      text-shadow: 0 0 10px rgba(139, 115, 85, 0.3);
    }
  }

  .mobile-menu {
    background: rgba(245, 242, 237, 0.98);
    border-bottom: 1px solid var(--border-color);

    .nav-link {
      color: var(--text-secondary);
      border-bottom: 1px solid var(--border-light);

      &:hover {
        color: var(--primary);
        background: rgba(139, 115, 85, 0.08);
      }

      &.active {
        color: var(--primary);
        background: rgba(139, 115, 85, 0.08);
      }
    }
  }

  // Custom cursor
  .cursor-dot {
    background: var(--primary) !important;
    box-shadow: 0 0 15px rgba(139, 115, 85, 0.4) !important;
  }

  .cursor-ring {
    border-color: var(--primary) !important;
  }
}
