.projects-section {
  min-height: 100vh;
  padding: 100px 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-header {
  text-align: center;
  margin-bottom: 4rem;
}

.section-label {
  display: inline-block;
  font-size: 0.85rem;
  font-weight: 500;
  color: #00ff00;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 1rem;
  padding: 0.5rem 1rem;
  background: rgba(0, 255, 0, 0.1);
  border: 1px solid rgba(0, 255, 0, 0.2);
  border-radius: 20px;
}

.section-title {
  font-size: clamp(2.5rem, 6vw, 4rem);
  font-weight: 700;
  color: #00ff00;
  margin-bottom: 1rem;
  text-shadow: 0 0 30px rgba(0, 255, 0, 0.4);
}

.section-subtitle {
  font-size: 1.2rem;
  color: rgba(255, 255, 255, 0.6);
  max-width: 500px;
  margin: 0 auto;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.5rem;
  max-width: 1200px;
  width: 100%;
  perspective: 1000px;

  @media (max-width: 900px) {
    grid-template-columns: 1fr;
    max-width: 600px;
  }
}

.project-card-wrapper {
  will-change: transform;
}

.project-card {
  position: relative;
  padding: 2.5rem;
  background: rgba(10, 10, 10, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 420px;
  transform-style: preserve-3d;
  transition: border-color 0.3s ease;

  &:hover {
    border-color: var(--accent-color, #00ff00);
  }
}

.card-bg-gradient {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(
    circle at 30% 20%,
    var(--accent-color, #00ff00) 0%,
    transparent 60%
  );
  pointer-events: none;
  z-index: 0;
}

.particles {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
}

.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: var(--accent-color, #00ff00);
  border-radius: 50%;
  box-shadow: 0 0 10px var(--accent-color, #00ff00);
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1.5rem;
  position: relative;
  z-index: 2;
}

.project-icon {
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  font-size: 1.75rem;
  color: var(--accent-color, #00ff00);
  transition: all 0.3s ease;

  .project-card:hover & {
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 0 0 30px rgba(var(--accent-color, 0, 255, 0), 0.3);
  }
}

.project-links {
  display: flex;
  gap: 0.75rem;

  a {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    color: rgba(255, 255, 255, 0.7);
    font-size: 1.1rem;
    transition: all 0.3s ease;

    &:hover {
      background: var(--accent-color, #00ff00);
      color: #000;
      border-color: var(--accent-color, #00ff00);
    }
  }
}

.project-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #fff;
  margin-bottom: 0.5rem;
  position: relative;
  z-index: 2;
}

.project-subtitle {
  font-size: 0.9rem;
  font-weight: 500;
  color: var(--accent-color, #00ff00);
  margin-bottom: 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  position: relative;
  z-index: 2;
}

.project-description {
  font-size: 0.95rem;
  color: rgba(255, 255, 255, 0.7);
  line-height: 1.7;
  margin-bottom: 1.5rem;
  position: relative;
  z-index: 2;
}

.project-metrics {
  display: flex;
  gap: 1.5rem;
  margin-bottom: 1.5rem;
  position: relative;
  z-index: 2;
}

.metric {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 12px;
  min-width: 80px;
  transition: all 0.3s ease;

  .project-card:hover & {
    background: rgba(255, 255, 255, 0.05);
    border-color: rgba(255, 255, 255, 0.15);
  }
}

.metric-value {
  font-size: 1.25rem;
  font-weight: 700;
  color: var(--accent-color, #00ff00);
  margin-bottom: 0.25rem;
}

.metric-label {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.5);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: auto;
  position: relative;
  z-index: 2;
}

.tag {
  font-size: 0.75rem;
  padding: 0.4rem 0.85rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  color: rgba(255, 255, 255, 0.7);
  transition: all 0.3s ease;
  cursor: default;

  &:hover {
    transform: translateY(-2px);
  }
}

.accent-line {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(
    90deg,
    transparent,
    var(--accent-color, #00ff00),
    transparent
  );
  transform-origin: left;
}

.view-more {
  margin-top: 4rem;

  a {
    display: inline-flex;
    align-items: center;
    gap: 0.75rem;
    padding: 1rem 2rem;
    background: transparent;
    border: 1px solid rgba(0, 255, 0, 0.3);
    border-radius: 30px;
    color: #00ff00;
    font-size: 1rem;
    font-weight: 500;
    text-decoration: none;
    transition: all 0.3s ease;

    svg {
      font-size: 1.25rem;
    }

    &:hover {
      background: rgba(0, 255, 0, 0.1);
      border-color: #00ff00;
      box-shadow: 0 0 30px rgba(0, 255, 0, 0.2);
    }
  }
}

@media (max-width: 600px) {
  .project-card {
    padding: 1.75rem;
    min-height: auto;
  }

  .project-metrics {
    flex-wrap: wrap;
    gap: 0.75rem;
  }

  .metric {
    flex: 1;
    min-width: 70px;
  }
}
