// Light theme button styles - Ink/Brush stroke effects

.btn-ink {
  position: relative;
  padding: 0.875rem 2rem;
  font-size: 0.9rem;
  font-weight: 500;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.4s ease;
  letter-spacing: 0.5px;

  // Ink spread effect on hover
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 0;
    height: 0;
    background: radial-gradient(circle, currentColor 0%, transparent 70%);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: all 0.5s ease;
  }

  &:hover::before {
    width: 300%;
    height: 300%;
    opacity: 0.1;
  }

  // Brush stroke underline
  &::after {
    content: '';
    position: absolute;
    bottom: 6px;
    left: 50%;
    width: 0;
    height: 2px;
    background: linear-gradient(90deg,
      transparent 0%,
      currentColor 20%,
      currentColor 80%,
      transparent 100%
    );
    transform: translateX(-50%);
    transition: width 0.3s ease;
    border-radius: 2px;
  }

  &:hover::after {
    width: 60%;
  }
}

// Primary button - filled style
.btn-ink-primary {
  @extend .btn-ink;
  background: #8b7355;
  color: white;

  &::after {
    background: linear-gradient(90deg,
      transparent 0%,
      rgba(255, 255, 255, 0.5) 20%,
      rgba(255, 255, 255, 0.5) 80%,
      transparent 100%
    );
  }

  &:hover {
    background: #6b5344;
    box-shadow:
      0 4px 20px rgba(139, 115, 85, 0.25),
      0 0 0 3px rgba(139, 115, 85, 0.1);
  }

  &:active {
    transform: scale(0.98);
  }
}

// Secondary button - outlined style
.btn-ink-secondary {
  @extend .btn-ink;
  background: transparent;
  color: #8b7355;
  border: 1px solid #8b7355;

  &:hover {
    background: rgba(139, 115, 85, 0.05);
    border-color: #6b5344;
  }
}

// Ghost button - minimal style
.btn-ink-ghost {
  @extend .btn-ink;
  background: transparent;
  color: #8b7355;
  padding: 0.75rem 1.5rem;

  &::before {
    display: none;
  }

  &::after {
    bottom: 4px;
    height: 1px;
    background: linear-gradient(90deg,
      transparent 0%,
      #c4956a 50%,
      transparent 100%
    );
  }

  &:hover {
    color: #6b5344;
  }

  &:hover::after {
    width: 80%;
  }
}

// Watercolor spread effect button
.btn-watercolor {
  position: relative;
  padding: 0.875rem 2rem;
  font-size: 0.9rem;
  font-weight: 500;
  background: transparent;
  border: none;
  color: #8b7355;
  cursor: pointer;
  overflow: hidden;
  transition: color 0.3s ease;

  // Watercolor blob background
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 120%;
    height: 150%;
    background:
      radial-gradient(ellipse at 30% 40%, rgba(196, 149, 106, 0.15) 0%, transparent 50%),
      radial-gradient(ellipse at 70% 60%, rgba(181, 116, 90, 0.1) 0%, transparent 50%),
      radial-gradient(ellipse at 50% 50%, rgba(139, 115, 85, 0.08) 0%, transparent 60%);
    transform: translate(-50%, -50%) scale(0);
    opacity: 0;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    border-radius: 60% 40% 50% 50% / 40% 50% 50% 60%;
  }

  &:hover::before {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
  }

  // Text wrapper
  span {
    position: relative;
    z-index: 1;
  }

  &:hover {
    color: #6b5344;
  }
}

// Brush stroke button
.btn-brush {
  position: relative;
  padding: 0.875rem 2rem;
  font-size: 0.9rem;
  font-weight: 500;
  background: transparent;
  border: none;
  color: #3d3d3d;
  cursor: pointer;
  overflow: visible;

  // Brush stroke background
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: -5%;
    width: 110%;
    height: 100%;
    background: linear-gradient(90deg,
      rgba(196, 149, 106, 0) 0%,
      rgba(196, 149, 106, 0.2) 10%,
      rgba(196, 149, 106, 0.25) 50%,
      rgba(196, 149, 106, 0.2) 90%,
      rgba(196, 149, 106, 0) 100%
    );
    border-radius: 2px 8px 2px 8px;
    transform: scaleX(0);
    transform-origin: left;
    transition: transform 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    z-index: -1;
  }

  &:hover::before {
    transform: scaleX(1);
  }

  // Rough edge effect
  &::after {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 10%;
    width: 80%;
    height: 3px;
    background: linear-gradient(90deg,
      transparent 0%,
      #c4956a 5%,
      #b5745a 30%,
      #c4956a 50%,
      #8b7355 70%,
      #c4956a 95%,
      transparent 100%
    );
    opacity: 0;
    transform: scaleX(0.5);
    transition: all 0.3s ease 0.1s;
  }

  &:hover::after {
    opacity: 0.6;
    transform: scaleX(1);
  }

  span {
    position: relative;
    z-index: 1;
  }
}

// Card hover effect for light theme
.card-light-hover {
  transition: all 0.3s ease;

  &:hover {
    border-color: rgba(139, 115, 85, 0.25);
    box-shadow:
      0 8px 30px rgba(100, 80, 60, 0.08),
      0 0 0 1px rgba(139, 115, 85, 0.05);

    // Subtle lift effect
    transform: translateY(-2px);
  }

  // Ink corner decoration on hover
  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 30px 30px 0;
    border-color: transparent rgba(139, 115, 85, 0.08) transparent transparent;
    opacity: 0;
    transition: opacity 0.3s ease;
  }

  &:hover::before {
    opacity: 1;
  }
}

// Link with brush underline
.link-brush {
  position: relative;
  color: #8b7355;
  text-decoration: none;
  transition: color 0.3s ease;

  &::after {
    content: '';
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg,
      #c4956a 0%,
      #b5745a 30%,
      #8b7355 60%,
      #c4956a 100%
    );
    transform: scaleX(0);
    transform-origin: right;
    transition: transform 0.3s ease;
    border-radius: 1px;
  }

  &:hover {
    color: #6b5344;

    &::after {
      transform: scaleX(1);
      transform-origin: left;
    }
  }
}
