.about-section-light {
  min-height: 100vh;
  padding: 100px 2rem;
  position: relative;
  background: transparent;
  overflow: hidden;
}

.about-container {
  max-width: 900px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

// Chapter Header
.chapter-header {
  text-align: center;
  margin-bottom: 4rem;
}

.chapter-number {
  display: inline-block;
  font-size: 0.7rem;
  font-weight: 400;
  color: #8b7355;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 0.75rem;
  font-style: italic;
}

.chapter-title {
  font-size: clamp(2rem, 5vw, 3rem);
  font-weight: 300;
  color: #3d3d3d;
  letter-spacing: -0.02em;
}

// Story Layout
.story-layout {
  display: grid;
  grid-template-columns: 1fr 200px;
  gap: 4rem;
  align-items: start;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    gap: 2.5rem;
  }
}

// Main Story
.story-main {
  max-width: 600px;
}

.narrative {
  p {
    font-size: 1rem;
    color: #5a5a5a;
    line-height: 1.9;
    margin-bottom: 1.5rem;

    em {
      color: #8b7355;
      font-style: normal;
      font-weight: 500;
    }
  }

  .lead {
    font-size: 1.15rem;
    color: #3d3d3d;
    font-weight: 400;
    line-height: 1.8;
  }
}

// Signature
.signature {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-top: 2.5rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(139, 115, 85, 0.1);
}

.initials {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #8b7355 0%, #c4956a 100%);
  border-radius: 50%;
  color: white;
  font-size: 1rem;
  font-weight: 500;
}

.signature .name {
  font-size: 0.9rem;
  color: #7a7a7a;
  font-style: italic;
}

// Side Aside
.story-aside {
  position: sticky;
  top: 120px;

  @media (max-width: 768px) {
    position: static;
  }
}

.aside-label {
  display: block;
  font-size: 0.65rem;
  font-weight: 500;
  color: #9a9a9a;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 1.5rem;
}

.metrics-stack {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;

  @media (max-width: 768px) {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1rem;
  }
}

.metric-item {
  text-align: left;

  @media (max-width: 768px) {
    text-align: center;
    flex: 1;
    min-width: 80px;
  }
}

.metric-value {
  display: block;
  font-size: 1.75rem;
  font-weight: 300;
  color: #8b7355;
  line-height: 1;
  margin-bottom: 0.25rem;
}

.metric-label {
  font-size: 0.7rem;
  color: #9a9a9a;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

// Chapter Quote
.chapter-quote {
  margin-top: 4rem;
  padding: 2rem 0;
  text-align: center;

  blockquote {
    font-size: 1.1rem;
    font-style: italic;
    color: #7a7a7a;
    line-height: 1.8;
    max-width: 500px;
    margin: 0 auto;
    position: relative;

    &::before {
      content: '"';
      position: absolute;
      top: -20px;
      left: -15px;
      font-size: 4rem;
      color: rgba(139, 115, 85, 0.1);
      font-family: Georgia, serif;
      line-height: 1;
    }
  }
}

// Responsive
@media (max-width: 768px) {
  .about-section-light {
    padding: 80px 1.5rem;
  }

  .chapter-header {
    margin-bottom: 3rem;
  }

  .chapter-title {
    font-size: 2rem;
  }

  .narrative .lead {
    font-size: 1.05rem;
  }
}
