.projects-section-light {
  min-height: 100vh;
  padding: 100px 2rem;
  position: relative;
  overflow: hidden;
  background: transparent;
}

.projects-bg {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

// Chapter Header
.chapter-header {
  text-align: center;
  margin-bottom: 4rem;
  position: relative;
  z-index: 1;
}

.chapter-number {
  display: inline-block;
  font-size: 0.7rem;
  font-weight: 400;
  color: #8b7355;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 0.75rem;
  font-style: italic;
}

.chapter-title {
  font-size: clamp(2rem, 5vw, 3rem);
  font-weight: 300;
  color: #3d3d3d;
  letter-spacing: -0.02em;
  margin-bottom: 0.5rem;
}

.chapter-subtitle {
  font-size: 0.95rem;
  color: #7a7a7a;
}

.projects-grid-light {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.75rem;
  max-width: 950px;
  margin: 0 auto 3rem;
  position: relative;
  z-index: 1;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    max-width: 450px;
  }
}

.project-card-light {
  background: rgba(255, 252, 247, 0.95);
  border-radius: 12px;
  padding: 1.75rem;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  box-shadow: 0 4px 25px rgba(100, 80, 60, 0.05);
  border: 1px solid rgba(139, 115, 85, 0.1);

  &:hover {
    border-color: var(--accent, #8b7355);
    box-shadow: 0 12px 40px rgba(100, 80, 60, 0.12);

    .card-accent-line {
      transform: scaleX(1);
    }

    .view-more {
      color: var(--accent, #8b7355);
      svg { transform: translateX(4px); }
    }
  }

  // Glow effect on hover
  .card-glow {
    position: absolute;
    inset: 0;
    background: radial-gradient(circle at 50% 0%, var(--accent, #8b7355), transparent 70%);
    pointer-events: none;
  }
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1.25rem;
}

.card-icon {
  width: 50px;
  height: 50px;
  background: rgba(139, 115, 85, 0.08);
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.25rem;
  color: var(--accent, #8b7355);
  position: relative;

  // Pulse ring animation
  .icon-pulse {
    position: absolute;
    inset: 0;
    border: 2px solid var(--accent, #8b7355);
    border-radius: 10px;
    pointer-events: none;
  }
}

.card-category {
  font-size: 0.65rem;
  font-weight: 500;
  color: var(--accent, #8b7355);
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 0.3rem 0.6rem;
  background: rgba(139, 115, 85, 0.06);
  border-radius: 3px;
}

.card-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #3d3d3d;
  margin-bottom: 0.5rem;
}

.card-description {
  font-size: 0.875rem;
  color: #5a5a5a;
  line-height: 1.6;
  margin-bottom: 1rem;
}

.card-metrics {
  display: flex;
  gap: 0.75rem;
  margin-bottom: 1rem;

  .metric-pill {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.5rem 0.75rem;
    background: rgba(139, 115, 85, 0.06);
    border-radius: 6px;
    min-width: 70px;

    .metric-value {
      font-size: 0.9rem;
      font-weight: 600;
      color: var(--accent, #8b7355);
    }

    .metric-label {
      font-size: 0.6rem;
      color: #9a9a9a;
      text-transform: capitalize;
    }
  }
}

.card-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
  margin-bottom: 1.25rem;

  .tag {
    padding: 0.25rem 0.6rem;
    background: rgba(139, 115, 85, 0.05);
    border-radius: 3px;
    font-size: 0.7rem;
    font-weight: 400;
    color: #7a7a7a;
  }
}

.card-footer {
  display: flex;
  justify-content: flex-end;
}

.view-more {
  display: flex;
  align-items: center;
  gap: 0.4rem;
  font-size: 0.8rem;
  font-weight: 400;
  color: #9a9a9a;
  transition: all 0.3s ease;

  svg {
    font-size: 0.65rem;
    transition: transform 0.3s ease;
  }
}

.card-accent-line {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, var(--accent, #8b7355), #c4956a);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s ease;
}

.projects-cta {
  text-align: center;
  position: relative;
  z-index: 1;

  .github-link {
    display: inline-flex;
    align-items: center;
    gap: 0.6rem;
    padding: 0.875rem 1.75rem;
    background: #8b7355;
    color: white;
    text-decoration: none;
    border-radius: 4px;
    font-weight: 500;
    font-size: 0.9rem;
    box-shadow: 0 4px 20px rgba(139, 115, 85, 0.2);
    transition: all 0.3s ease;

    svg { font-size: 1.1rem; }

    &:hover {
      background: #6b5344;
      box-shadow: 0 6px 25px rgba(139, 115, 85, 0.3);
    }
  }
}

// Modal Styles
.project-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(61, 61, 61, 0.4);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 2rem;
}

.project-modal {
  background: #fffcf7;
  border-radius: 16px;
  padding: 2.5rem;
  max-width: 550px;
  width: 100%;
  position: relative;
  max-height: 85vh;
  overflow-y: auto;
  border: 1px solid rgba(139, 115, 85, 0.15);
}

.modal-close {
  position: absolute;
  top: 1.25rem;
  right: 1.25rem;
  width: 36px;
  height: 36px;
  border: none;
  background: rgba(139, 115, 85, 0.08);
  border-radius: 50%;
  font-size: 1.25rem;
  color: #8b7355;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    background: #8b7355;
    color: white;
  }
}

.modal-header {
  display: flex;
  align-items: center;
  gap: 1.25rem;
  margin-bottom: 1.25rem;

  .modal-icon {
    width: 55px;
    height: 55px;
    background: var(--accent, #8b7355);
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    color: white;
  }

  .modal-category {
    font-size: 0.7rem;
    font-weight: 500;
    color: var(--accent, #8b7355);
    text-transform: uppercase;
    letter-spacing: 1px;
  }

  h2 {
    font-size: 1.5rem;
    font-weight: 600;
    color: #3d3d3d;
    margin: 0.2rem 0 0;
  }
}

.modal-description {
  font-size: 0.925rem;
  color: #5a5a5a;
  line-height: 1.8;
  margin-bottom: 1.75rem;
}

.modal-metrics {
  display: flex;
  gap: 1.25rem;
  margin-bottom: 1.75rem;
  flex-wrap: wrap;

  .metric {
    text-align: center;
    padding: 0.875rem 1.25rem;
    background: rgba(139, 115, 85, 0.05);
    border-radius: 8px;
    flex: 1;
    min-width: 90px;

    .metric-value {
      display: block;
      font-size: 1.25rem;
      font-weight: 600;
      color: var(--accent, #8b7355);
    }

    .metric-label {
      font-size: 0.65rem;
      color: #9a9a9a;
      text-transform: uppercase;
      letter-spacing: 0.5px;
    }
  }
}

.modal-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.4rem;
  margin-bottom: 1.5rem;

  .tag {
    padding: 0.4rem 0.8rem;
    background: rgba(139, 115, 85, 0.06);
    border-radius: 4px;
    font-size: 0.8rem;
    font-weight: 400;
    color: #5a5a5a;
  }
}

.modal-highlights {
  margin-bottom: 1.75rem;
  padding: 1rem 1.25rem;
  background: rgba(139, 115, 85, 0.04);
  border-radius: 8px;
  border-left: 3px solid var(--accent, #8b7355);

  h4 {
    font-size: 0.75rem;
    font-weight: 600;
    color: #8b7355;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-bottom: 0.75rem;
  }

  ul {
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      font-size: 0.85rem;
      color: #5a5a5a;
      padding: 0.35rem 0;
      padding-left: 1.25rem;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        left: 0;
        color: var(--accent, #8b7355);
        font-size: 0.75rem;
      }
    }
  }
}

.modal-github {
  display: inline-flex;
  align-items: center;
  gap: 0.6rem;
  padding: 0.75rem 1.5rem;
  background: #3d3d3d;
  color: white;
  text-decoration: none;
  border-radius: 4px;
  font-weight: 500;
  font-size: 0.875rem;
  transition: all 0.3s ease;

  svg { font-size: 1rem; }

  &:hover {
    background: #8b7355;
  }
}
