.story-scene--arsenal {
  min-height: 100vh;
  padding: 100px 2rem;
  position: relative;
  overflow: hidden;
}

.floating-icons {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 0;
}

.floating-icon {
  position: absolute;
  font-size: 3rem;
  color: #00ff00;
  opacity: 0.1;
}

.arsenal-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
  max-width: 1100px;
  margin: 0 auto 3rem;
  position: relative;
  z-index: 1;

  @media (max-width: 900px) {
    grid-template-columns: 1fr;
  }
}

.category-section {
  background: rgba(10, 10, 10, 0.6);
  border: 1px solid rgba(0, 255, 0, 0.15);
  border-radius: 20px;
  padding: 2rem;
  backdrop-filter: blur(10px);
}

.category-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid rgba(0, 255, 0, 0.1);

  .category-icon {
    font-size: 1.5rem;
    color: #00ff00;
  }

  h3 {
    font-size: 1.1rem;
    font-weight: 600;
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
}

.tools-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 1rem;
}

.tool-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-radius: 12px;
  cursor: default;
  transition: all 0.3s ease;

  &:hover {
    border-color: rgba(0, 255, 0, 0.3);
    background: rgba(0, 255, 0, 0.05);
  }
}

.tool-icon {
  font-size: 1.75rem;
  color: #00ff00;
  margin-bottom: 0.5rem;
}

.tool-name {
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.8);
  text-align: center;
  margin-bottom: 0.5rem;
}

.skill-bar {
  width: 100%;
  height: 4px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 2px;
  overflow: hidden;
  margin-bottom: 0.25rem;
}

.skill-fill {
  height: 100%;
  background: linear-gradient(90deg, #00ff00, #00cc00);
  border-radius: 2px;
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
}

.skill-level {
  font-size: 0.65rem;
  color: rgba(0, 255, 0, 0.7);
  font-weight: 600;
}

.terminal-showcase {
  max-width: 600px;
  margin: 3rem auto 2rem;
  background: rgba(0, 0, 0, 0.8);
  border: 1px solid rgba(0, 255, 0, 0.2);
  border-radius: 12px;
  overflow: hidden;
  font-family: 'Fira Code', 'Monaco', monospace;
}

.terminal-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  background: rgba(0, 0, 0, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);

  .dot {
    width: 12px;
    height: 12px;
    border-radius: 50%;

    &.red { background: #ff5f56; }
    &.yellow { background: #ffbd2e; }
    &.green { background: #27ca40; }
  }

  .terminal-title {
    margin-left: auto;
    font-size: 0.75rem;
    color: rgba(255, 255, 255, 0.4);
  }
}

.terminal-body {
  padding: 1.25rem;
  font-size: 0.85rem;
  line-height: 1.8;

  .prompt {
    color: #00ff00;
    margin-right: 0.5rem;
  }

  .output {
    color: rgba(255, 255, 255, 0.7);
    padding-left: 1rem;
  }

  .success {
    color: #00ff00;
    font-weight: 600;
  }
}
