.skills-constellation {
  padding: 3rem 0;
  position: relative;
}

.constellation-container {
  position: relative;
  width: 100%;
  max-width: 400px;
  height: 400px;
  margin: 0 auto;

  @media (max-width: 768px) {
    display: none;
  }
}

.center-orb {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: linear-gradient(135deg, #f5f2ed 0%, #ebe6df 100%);
  border: 2px solid rgba(139, 115, 85, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: 500;
  color: #8b7355;
  text-transform: uppercase;
  letter-spacing: 1px;
  z-index: 10;
  transition: all 0.3s ease;
}

.category-orbit {
  position: absolute;
  inset: 0;
}

.connection-line {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.category-node {
  position: absolute;
  transform: translate(-50%, -50%);
  padding: 0.6rem 1rem;
  background: rgba(255, 252, 247, 0.9);
  border: 1px solid rgba(139, 115, 85, 0.15);
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 500;
  color: var(--category-color, #8b7355);
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 5;
  white-space: nowrap;

  &:hover {
    background: var(--category-color);
    color: white;
    border-color: var(--category-color);
    box-shadow: 0 4px 15px rgba(139, 115, 85, 0.2);
  }

  &.active {
    background: var(--category-color);
    color: white;
    border-color: var(--category-color);
    box-shadow: 0 4px 20px rgba(139, 115, 85, 0.25);
  }
}

.skill-nodes {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.skill-node {
  position: absolute;
  transform: translate(-50%, -50%);
  padding: 0.4rem 0.8rem;
  background: rgba(255, 252, 247, 0.95);
  border: 1px solid rgba(139, 115, 85, 0.1);
  border-radius: 4px;
  font-size: 0.7rem;
  font-weight: 400;
  color: #5a5a5a;
  cursor: default;
  pointer-events: auto;
  white-space: nowrap;
  transition: all 0.2s ease;

  &:hover {
    border-color: var(--skill-color);
    color: var(--skill-color);
    transform: translate(-50%, -50%) scale(1.05);
  }
}

.skill-glow {
  position: absolute;
  inset: -4px;
  background: var(--skill-color);
  opacity: 0.1;
  border-radius: 6px;
  z-index: -1;
}

.constellation-particles {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.particle {
  position: absolute;
  width: 3px;
  height: 3px;
  background: rgba(196, 149, 106, 0.4);
  border-radius: 50%;
}

// Mobile pills layout
.skills-pills {
  display: none;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.75rem;
  margin-bottom: 1rem;

  @media (max-width: 768px) {
    display: flex;
  }
}

.skill-pill {
  padding: 0.6rem 1.25rem;
  background: rgba(255, 252, 247, 0.9);
  border: 1px solid rgba(139, 115, 85, 0.12);
  border-radius: 25px;
  font-size: 0.8rem;
  font-weight: 500;
  color: var(--pill-color, #8b7355);
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover,
  &.active {
    background: var(--pill-color);
    color: white;
    border-color: var(--pill-color);
  }
}

.skills-expanded {
  display: none;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.5rem;
  padding: 1rem;
  background: rgba(235, 230, 223, 0.3);
  border-radius: 8px;
  overflow: hidden;

  @media (max-width: 768px) {
    display: flex;
  }
}

.expanded-skill {
  padding: 0.4rem 0.8rem;
  background: rgba(255, 252, 247, 0.9);
  border: 1px solid rgba(139, 115, 85, 0.08);
  border-radius: 4px;
  font-size: 0.75rem;
  color: #5a5a5a;
}
