.story-scene--impact {
  min-height: 100vh;
  padding: 100px 2rem;
  position: relative;
  overflow: hidden;
}

.impact-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 2rem;
  max-width: 1200px;
  margin: 0 auto 4rem;
  perspective: 1000px;

  @media (max-width: 1100px) {
    grid-template-columns: repeat(2, 1fr);
  }

  @media (max-width: 600px) {
    grid-template-columns: 1fr;
    max-width: 400px;
  }
}

.impact-card {
  position: relative;
  padding: 2rem 1.5rem;
  background: rgba(10, 10, 10, 0.8);
  border: 1px solid rgba(255, 255, 255, 0.08);
  border-radius: 20px;
  text-align: center;
  overflow: hidden;
  transform-style: preserve-3d;
  cursor: default;

  &:hover {
    border-color: var(--accent-color, #00ff00);
  }
}

.card-glow {
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(
    circle at center,
    var(--accent-color, #00ff00) 0%,
    transparent 40%
  );
  opacity: 0.1;
  pointer-events: none;
}

.impact-icon {
  font-size: 2.5rem;
  color: var(--accent-color, #00ff00);
  margin-bottom: 1rem;
}

.impact-value {
  font-size: 3rem;
  font-weight: 800;
  color: #fff;
  margin-bottom: 0.5rem;
  text-shadow: 0 0 30px var(--accent-color, #00ff00);
}

.impact-label {
  font-size: 1rem;
  font-weight: 600;
  color: var(--accent-color, #00ff00);
  margin-bottom: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.impact-description {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.6);
  line-height: 1.6;
}

.card-accent {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--accent-color, #00ff00);
  transform-origin: left;
}

.quote-section {
  max-width: 800px;
  margin: 0 auto 3rem;
  padding: 3rem;
  background: rgba(0, 255, 0, 0.03);
  border: 1px solid rgba(0, 255, 0, 0.1);
  border-radius: 20px;
  text-align: center;
  position: relative;
}

.quote-marks {
  position: absolute;
  top: 1rem;
  left: 2rem;
  font-size: 5rem;
  color: rgba(0, 255, 0, 0.15);
  font-family: Georgia, serif;
  line-height: 1;
}

.quote-text {
  font-size: 1.25rem;
  color: rgba(255, 255, 255, 0.85);
  line-height: 1.8;
  font-style: italic;
  margin-bottom: 1.5rem;
}

.quote-author {
  font-size: 0.9rem;
  color: #00ff00;
  font-weight: 500;
}

.data-stream {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 0;
  overflow: hidden;
}

.stream-particle {
  position: absolute;
  left: 0;
  width: 50px;
  height: 2px;
  background: linear-gradient(90deg, transparent, #00ff00, transparent);
  opacity: 0.3;
}

.scene-narrative {
  position: relative;
  z-index: 1;
}
