.journey-section-light {
  min-height: 100vh;
  padding: 100px 2rem;
  position: relative;
  overflow: hidden;
  background: transparent;
}

.journey-container {
  max-width: 700px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

// Chapter Header
.chapter-header {
  text-align: center;
  margin-bottom: 4rem;
}

.chapter-number {
  display: inline-block;
  font-size: 0.7rem;
  font-weight: 400;
  color: #8b7355;
  text-transform: uppercase;
  letter-spacing: 3px;
  margin-bottom: 0.75rem;
  font-style: italic;
}

.chapter-title {
  font-size: clamp(2rem, 5vw, 3rem);
  font-weight: 300;
  color: #3d3d3d;
  letter-spacing: -0.02em;
}

// Book-style Timeline
.book-timeline {
  position: relative;
  padding-left: 100px;

  // Vertical line
  &::before {
    content: '';
    position: absolute;
    left: 85px;
    top: 0;
    bottom: 0;
    width: 1px;
    background: linear-gradient(
      180deg,
      transparent 0%,
      rgba(139, 115, 85, 0.2) 10%,
      rgba(139, 115, 85, 0.2) 90%,
      transparent 100%
    );
  }

  @media (max-width: 600px) {
    padding-left: 0;

    &::before {
      display: none;
    }
  }
}

.timeline-entry {
  position: relative;
  margin-bottom: 3rem;

  &:last-child {
    margin-bottom: 0;
  }
}

.entry-marker {
  position: absolute;
  left: -100px;
  top: 0;
  width: 85px;
  text-align: right;
  padding-right: 20px;

  @media (max-width: 600px) {
    position: static;
    width: auto;
    text-align: left;
    padding: 0;
    margin-bottom: 0.75rem;
  }
}

.marker-year {
  font-size: 0.75rem;
  font-weight: 500;
  color: #8b7355;
  letter-spacing: 0.5px;
}

.marker-line {
  display: none;
}

.entry-content {
  padding-left: 20px;
  border-left: 2px solid transparent;
  transition: border-color 0.3s ease;

  &:hover {
    border-left-color: rgba(139, 115, 85, 0.15);
  }

  @media (max-width: 600px) {
    padding-left: 15px;
    border-left: 2px solid rgba(139, 115, 85, 0.1);
  }
}

.entry-title {
  font-size: 1.25rem;
  font-weight: 500;
  color: #3d3d3d;
  margin-bottom: 0.5rem;
}

.entry-meta {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.75rem;
  flex-wrap: wrap;
}

.entry-role {
  font-size: 0.85rem;
  font-weight: 500;
  color: #8b7355;
}

.entry-divider {
  color: #c4956a;
  font-size: 0.8rem;
}

.entry-place {
  font-size: 0.85rem;
  color: #7a7a7a;
}

.entry-narrative {
  font-size: 0.925rem;
  color: #5a5a5a;
  line-height: 1.8;
}

// Epilogue
.journey-epilogue {
  text-align: center;
  margin-top: 4rem;
  padding-top: 2rem;
}

.epilogue-line {
  display: block;
  width: 40px;
  height: 1px;
  background: linear-gradient(
    90deg,
    transparent 0%,
    rgba(139, 115, 85, 0.3) 50%,
    transparent 100%
  );
  margin: 0 auto 1rem;
}

.journey-epilogue p {
  font-size: 0.85rem;
  color: #9a9a9a;
  font-style: italic;
}

// Responsive
@media (max-width: 768px) {
  .journey-section-light {
    padding: 80px 1.5rem;
  }

  .chapter-header {
    margin-bottom: 3rem;
  }

  .chapter-title {
    font-size: 2rem;
  }
}
