// Bird Journey - Complete lifecycle with seasons and day/night
// Only visible in light theme

.bird-journey {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
  overflow: hidden;

  // Hide in dark theme
  [data-theme="dark"] & {
    display: none;
  }
}

// === SKY GRADIENTS ===
.sky-layer {
  position: absolute;
  inset: 0;
  transition: opacity 0.5s ease;
}

.dawn-sky {
  background: linear-gradient(
    180deg,
    #fdf8f3 0%,
    #f8e8d8 30%,
    #f0d8c8 60%,
    #e8c8b8 100%
  );
}

.day-sky {
  background: linear-gradient(
    180deg,
    #f5f2ed 0%,
    #ebe6df 40%,
    #e0dbd4 100%
  );
}

.sunset-sky {
  background: linear-gradient(
    180deg,
    #e8d8c8 0%,
    #d8b8a0 30%,
    #c89878 50%,
    #b87858 70%,
    #a86848 100%
  );
}

.night-sky {
  background: linear-gradient(
    180deg,
    #4a4a5a 0%,
    #3a3a4a 30%,
    #2a2a3a 60%,
    #1a1a2a 100%
  );
}

.new-dawn-sky {
  background: linear-gradient(
    180deg,
    #fdf8f3 0%,
    #f8ece0 30%,
    #f0e0d0 60%,
    #e8d4c4 100%
  );
}

// === CELESTIAL BODIES ===
.celestial {
  position: absolute;
  pointer-events: none;

  &.sun {
    right: 10%;
    width: 140px;
    height: 140px;
    animation: sunFloat 6s ease-in-out infinite;
    filter: drop-shadow(0 0 30px rgba(196, 149, 106, 0.6));

    .sun-rays {
      transform-origin: 70px 70px;
      animation: sunRaysRotate 30s linear infinite;

      line {
        animation: sunRaysPulse 2s ease-in-out infinite;

        &:nth-child(odd) {
          animation-delay: 0.5s;
        }
      }
    }
  }

  &.sunset-sun {
    right: 50%;
    bottom: 12%;
    transform: translateX(50%);
    width: 180px;
    height: 180px;
    animation: sunsetPulse 3s ease-in-out infinite;
    filter: drop-shadow(0 0 40px rgba(181, 116, 90, 0.7));
  }

  &.moon {
    right: 10%;
    width: 160px;
    height: 160px;
    animation: moonFloat 8s ease-in-out infinite;
    filter: drop-shadow(0 0 25px rgba(235, 230, 223, 0.6));

    // Pulsing rings
    .moon-ring {
      transform-origin: 80px 80px;

      &.ring-1 {
        animation: ringPulse1 4s ease-in-out infinite;
      }
      &.ring-2 {
        animation: ringPulse2 4s ease-in-out infinite 0.5s;
      }
      &.ring-3 {
        animation: ringPulse3 4s ease-in-out infinite 1s;
      }
    }

    // UFO animation
    .ufo {
      animation: ufoFly 15s ease-in-out infinite;

      .ufo-beam {
        animation: beamScan 15s ease-in-out infinite;
      }

      .ufo-light {
        animation: ufoLights 0.5s ease-in-out infinite;

        &.ufo-light-2 { animation-delay: 0.15s; }
        &.ufo-light-3 { animation-delay: 0.3s; }
      }
    }
  }
}

@keyframes sunFloat {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-10px); }
}

@keyframes sunRaysRotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes sunRaysPulse {
  0%, 100% { opacity: 0.4; }
  50% { opacity: 0.8; }
}

@keyframes sunsetPulse {
  0%, 100% {
    transform: translateX(50%) scale(1);
    filter: drop-shadow(0 0 40px rgba(181, 116, 90, 0.7));
  }
  50% {
    transform: translateX(50%) scale(1.08);
    filter: drop-shadow(0 0 60px rgba(181, 116, 90, 0.9));
  }
}

@keyframes moonFloat {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-6px); }
}

// Pulsing ring animations - expand and fade
@keyframes ringPulse1 {
  0%, 100% {
    transform: scale(1);
    opacity: 0.4;
  }
  50% {
    transform: scale(1.15);
    opacity: 0.1;
  }
}

@keyframes ringPulse2 {
  0%, 100% {
    transform: scale(1);
    opacity: 0.3;
  }
  50% {
    transform: scale(1.12);
    opacity: 0.08;
  }
}

@keyframes ringPulse3 {
  0%, 100% {
    transform: scale(1);
    opacity: 0.2;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.05;
  }
}

// UFO flies in, scans moon, flies away
@keyframes ufoFly {
  0% {
    transform: translate(-60px, -20px);
    opacity: 0;
  }
  8% {
    transform: translate(-40px, -10px);
    opacity: 0;
  }
  15% {
    transform: translate(-20px, 0px);
    opacity: 1;
  }
  // Arrive and hover over moon
  30% {
    transform: translate(50px, 40px);
    opacity: 1;
  }
  // Scanning phase - slight wobble
  40% {
    transform: translate(52px, 38px);
    opacity: 1;
  }
  50% {
    transform: translate(48px, 42px);
    opacity: 1;
  }
  60% {
    transform: translate(50px, 40px);
    opacity: 1;
  }
  // Leave
  75% {
    transform: translate(120px, 10px);
    opacity: 1;
  }
  85% {
    transform: translate(150px, -10px);
    opacity: 0;
  }
  100% {
    transform: translate(160px, -20px);
    opacity: 0;
  }
}

// Beam appears during scanning phase
@keyframes beamScan {
  0%, 25% {
    opacity: 0;
    transform: scaleY(0);
  }
  35% {
    opacity: 0.5;
    transform: scaleY(1);
  }
  55% {
    opacity: 0.5;
    transform: scaleY(1);
  }
  65%, 100% {
    opacity: 0;
    transform: scaleY(0);
  }
}

// UFO lights blinking
@keyframes ufoLights {
  0%, 100% {
    opacity: 0.5;
    fill: #c4956a;
  }
  50% {
    opacity: 1;
    fill: #d4a57a;
  }
}

// === STARS ===
.stars-container {
  position: absolute;
  inset: 0;
}

.star {
  position: absolute;
  width: 3px;
  height: 3px;
  background: #ebe6df;
  border-radius: 50%;
  animation: twinkle 2s ease-in-out infinite;

  &::after {
    content: '';
    position: absolute;
    inset: -2px;
    background: radial-gradient(circle, rgba(235, 230, 223, 0.5) 0%, transparent 70%);
  }
}

@keyframes twinkle {
  0%, 100% { opacity: 0.3; transform: scale(0.8); }
  50% { opacity: 1; transform: scale(1.2); }
}

// === SEASONS ===
.season {
  position: absolute;
  inset: 0;
  pointer-events: none;
}

// Spring
.spring-season {
  .cherry-blossoms {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .blossom {
    position: absolute;
    top: 10%;
    width: 8px;
    height: 8px;
    background: radial-gradient(circle, #f0d8d0 0%, #e8c8c0 50%, transparent 70%);
    border-radius: 50%;
    animation: floatDown 8s ease-in-out infinite;

    &::before, &::after {
      content: '';
      position: absolute;
      width: 6px;
      height: 6px;
      background: radial-gradient(circle, #f0d8d0 0%, transparent 70%);
      border-radius: 50%;
    }
    &::before { top: -4px; left: 2px; }
    &::after { top: 2px; left: -4px; }
  }

  .spring-tree {
    position: absolute;
    bottom: 20%;
    left: 15%;
    width: 4px;
    height: 80px;
    background: linear-gradient(180deg, #a89078 0%, #8b7355 100%);

    &::before {
      content: '';
      position: absolute;
      top: -30px;
      left: -25px;
      width: 54px;
      height: 54px;
      background: radial-gradient(circle, rgba(240, 200, 200, 0.6) 0%, rgba(232, 184, 184, 0.3) 50%, transparent 70%);
      border-radius: 50%;
    }
  }
}

@keyframes floatDown {
  0% { transform: translateY(0) rotate(0deg); opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 1; }
  100% { transform: translateY(400px) rotate(360deg); opacity: 0; }
}

// Summer
.summer-season {
  .summer-trees {
    position: absolute;
    bottom: 15%;
    width: 100%;
  }

  .tree {
    position: absolute;
    width: 5px;
    height: 100px;
    background: #8b7355;

    &::before {
      content: '';
      position: absolute;
      top: -40px;
      left: -30px;
      width: 65px;
      height: 65px;
      background: radial-gradient(circle, rgba(168, 144, 120, 0.5) 0%, rgba(168, 144, 120, 0.2) 60%, transparent 70%);
      border-radius: 50%;
    }

    &.tree-1 { left: 20%; }
    &.tree-2 { left: 75%; height: 120px; }
  }

  .butterflies {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .butterfly {
    position: absolute;
    top: 30%;
    left: 30%;
    width: 20px;
    height: 10px;
    animation: flutter 8s ease-in-out infinite;

    &::before, &::after {
      content: '';
      position: absolute;
      width: 8px;
      height: 12px;
      background: rgba(196, 149, 106, 0.6);
      border-radius: 50% 50% 50% 50% / 60% 60% 40% 40%;
      animation: wingFlap 0.3s ease-in-out infinite;
    }
    &::before { left: 0; transform-origin: right center; }
    &::after { right: 0; transform-origin: left center; animation-delay: 0.15s; }
  }
}

@keyframes flutter {
  0%, 100% { transform: translate(0, 0); }
  25% { transform: translate(50px, -30px); }
  50% { transform: translate(100px, 10px); }
  75% { transform: translate(50px, 30px); }
}

@keyframes wingFlap {
  0%, 100% { transform: rotateY(0deg); }
  50% { transform: rotateY(60deg); }
}

// Fall
.fall-season {
  .fall-trees {
    position: absolute;
    bottom: 15%;
    width: 100%;
  }

  .fall-tree {
    position: absolute;
    width: 5px;
    height: 90px;
    background: #8b7355;

    &::before {
      content: '';
      position: absolute;
      top: -35px;
      left: -28px;
      width: 60px;
      height: 55px;
      background: radial-gradient(circle, rgba(181, 116, 90, 0.5) 0%, rgba(196, 149, 106, 0.3) 50%, transparent 70%);
      border-radius: 50%;
    }

    &.fall-tree-1 { left: 25%; }
    &.fall-tree-2 { left: 70%; height: 110px; }
  }

  .falling-leaves {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .leaf {
    position: absolute;
    top: -5%;
    width: 10px;
    height: 10px;
    background: linear-gradient(135deg, #b5745a 0%, #c4956a 50%, #a89078 100%);
    border-radius: 0 50% 50% 50%;
    animation: leafFall 6s ease-in-out infinite;
    transform-origin: center;

    &:nth-child(odd) {
      background: linear-gradient(135deg, #c4956a 0%, #b5745a 100%);
    }
  }
}

@keyframes leafFall {
  0% { transform: translateY(0) rotate(0deg) translateX(0); opacity: 0; }
  10% { opacity: 1; }
  50% { transform: translateY(250px) rotate(180deg) translateX(30px); }
  90% { opacity: 1; }
  100% { transform: translateY(500px) rotate(360deg) translateX(-20px); opacity: 0; }
}

// Winter
.winter-season {
  .bare-trees {
    position: absolute;
    bottom: 15%;
    width: 100%;
  }

  .bare-tree {
    position: absolute;
    width: 4px;
    height: 80px;
    background: #6b5344;

    &::before, &::after {
      content: '';
      position: absolute;
      background: #6b5344;
      border-radius: 2px;
    }
    &::before {
      top: 20px;
      left: -15px;
      width: 20px;
      height: 3px;
      transform: rotate(-30deg);
    }
    &::after {
      top: 35px;
      right: -12px;
      width: 16px;
      height: 3px;
      transform: rotate(25deg);
    }

    &.bare-tree-1 { left: 22%; }
    &.bare-tree-2 { left: 72%; height: 100px; }
  }

  .snowflakes {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .snowflake {
    position: absolute;
    top: -5%;
    width: 6px;
    height: 6px;
    background: radial-gradient(circle, rgba(255, 255, 255, 0.9) 0%, rgba(235, 230, 223, 0.6) 50%, transparent 70%);
    border-radius: 50%;
    animation: snowFall 7s linear infinite;

    &:nth-child(odd) {
      width: 4px;
      height: 4px;
    }
  }

  .snow-ground {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 60px;
    background: linear-gradient(180deg, transparent 0%, rgba(255, 255, 255, 0.4) 30%, rgba(235, 230, 223, 0.6) 100%);
  }
}

@keyframes snowFall {
  0% { transform: translateY(0) translateX(0); opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 0.8; }
  100% { transform: translateY(600px) translateX(20px); opacity: 0; }
}

// New Spring
.new-spring-season {
  .new-blossoms {
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .new-blossom {
    position: absolute;
    top: 20%;
    width: 12px;
    height: 12px;
    background: radial-gradient(circle, #f8e8e0 0%, #f0d8d0 40%, transparent 70%);
    border-radius: 50%;
    animation: bloomPulse 2s ease-in-out infinite;

    &::before {
      content: '';
      position: absolute;
      inset: -4px;
      background: radial-gradient(circle, rgba(248, 232, 224, 0.3) 0%, transparent 70%);
      border-radius: 50%;
    }
  }
}

@keyframes bloomPulse {
  0%, 100% { transform: scale(1); opacity: 0.8; }
  50% { transform: scale(1.2); opacity: 1; }
}

// === NEST WITH EGG ===
.nest-container {
  position: absolute;
  bottom: 12%;
  left: 3%;
  width: 150px;
  height: 120px;

  .nest-svg {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 2px 4px rgba(139, 115, 85, 0.2));
  }

  .egg {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 50px;

    svg {
      filter: drop-shadow(0 2px 4px rgba(139, 115, 85, 0.15));
    }
  }

  @media (max-width: 768px) {
    width: 100px;
    height: 80px;
    bottom: 18%;

    .egg {
      width: 28px;
      height: 35px;
      top: 12px;
    }
  }
}

// === BIRD ===
.bird-container {
  position: fixed;
  width: 90px;
  height: 70px;
  transform-origin: center center;
  z-index: 5;

  .baby-bird, .adult-bird {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 2px 4px rgba(139, 115, 85, 0.3));
  }

  @media (max-width: 768px) {
    width: 60px;
    height: 50px;
  }
}

// === LANDING BRANCH ===
.landing-branch {
  position: absolute;
  top: 48%;
  right: 2%;
  width: 350px;
  height: 140px;

  .branch-svg {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 4px 8px rgba(139, 115, 85, 0.25));
  }

  @media (max-width: 768px) {
    width: 250px;
    height: 100px;
    top: 52%;
  }
}

// === FINAL BRANCH WITH BIRD (combined) ===
.final-branch-with-bird {
  position: absolute;
  top: 45%;
  right: 2%;
  width: 380px;
  height: 180px;

  .branch-bird-svg {
    width: 100%;
    height: 100%;
    filter: drop-shadow(0 4px 8px rgba(139, 115, 85, 0.25));
  }

  @media (max-width: 768px) {
    width: 280px;
    height: 130px;
    top: 50%;
  }
}

// === CLOUDS ===
.clouds-layer {
  position: absolute;
  top: 10%;
  left: 0;
  width: 100%;
  height: 200px;
  pointer-events: none;

  .cloud {
    position: absolute;

    &.cloud-1 {
      left: 8%;
      top: 20%;
      width: 180px;
      animation: cloudDrift 25s ease-in-out infinite;
    }

    &.cloud-2 {
      right: 15%;
      top: 50%;
      width: 150px;
      animation: cloudDrift 30s ease-in-out infinite reverse;
    }
  }
}

@keyframes cloudDrift {
  0%, 100% { transform: translateX(0); }
  50% { transform: translateX(30px); }
}

// === RESPONSIVE ===
@media (max-width: 768px) {
  .celestial {
    &.sun { width: 70px; height: 70px; right: 8%; }
    &.sunset-sun { width: 100px; height: 100px; }
    &.moon { width: 60px; height: 60px; }
  }

  .spring-season .spring-tree,
  .summer-season .tree,
  .fall-season .fall-tree,
  .winter-season .bare-tree {
    display: none;
  }

  .clouds-layer {
    .cloud {
      &.cloud-1 { width: 120px; }
      &.cloud-2 { width: 100px; }
    }
  }
}
